import { useNavigate } from "react-router-dom";
import "./footer.scss";
export default function Footer() {
  const navigate = useNavigate();
  return (
    <footer className="footer">
      <div className="mainLayout">
        <div className="contentWrapper">
          <p className="userInfo">개인정보처리방침</p>
          <div className="divider2">|</div>
          <p className="siteRule">울산하늘공원 운영규정</p>
          <div className="divider2">|</div>
          <p className="siteMap" onClick={() => navigate("/sitemap")}>
            사이트맵
          </p>
        </div>

        <div className="mainDivider"></div>

        <div className="footerWrapper">
          <div className="titleWrapper">
            <div className="image"></div>
            <div className="title"><p>울산 하늘공원 장례식장(주)</p></div>
          </div>

          <div className="footerContentWrapper">
            <div className="addressWrapper">
              <div className="address">
                (44956)울산광역시 울주군 삼동면 보삼길 550
              </div>
              <div className="footerCopyright">
                Copyright (c) ULSAN INFRASTRUCTURE CORPORATION(UIC). ALL rights
                reserved.
              </div>
            </div>
            <div className="numberWrapper">
              <div className="phone">문의 전화번호 : 052) 264-1017</div>
              <div className="fax">팩스번호 : 052)263-0040</div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}
